const btn = document.getElementById("download-btn");
let downloads = 0;

window.onload = (__event)=>{
  let url = "";
  if (navigator.appVersion.indexOf('Win') != -1) url = "https://github.com/R2rog/qwertyapp/releases/download/v1.0.10/the-qwerty-app.Setup.1.0.10.exe";
  else if (navigator.appVersion.indexOf('Mac')!= -1) url = "https://github.com/R2rog/qwertyapp/releases/download/v1.0.10/the-qwerty-app-1.0.10.dmg";
  else url = "https://github.com/R2rog/qwertyapp/releases/download/v1.0.10/qwerty_1.0.10_amd64.deb";
  btn.setAttribute('href',url);
};

